import React from 'react'
import "./nav.css"

export default function Nav() {


    const toggleTheNav =   ()=>{

        const NavToggle = document.querySelector('.mobile-nav-toggle');
        
        const primaryNav = document.querySelector('.primary-navigation');
        const visiblity = primaryNav.getAttribute('data-visible');
        console.log(NavToggle, primaryNav);

        if(visiblity === 'false'){
            primaryNav.setAttribute('data-visible', true);
            NavToggle.setAttribute('aria-expanded', true);
            console.log(1);
        } else if (visiblity === 'true'){
                primaryNav.setAttribute('data-visible', false)
                NavToggle.setAttribute('aria-expanded', false);
                console.log(2);
            }
    }

    return (   
        <header className="primary-header flex">
            <div>
                
                <img className='logo'src="./github.png" alt="site-logo" /></div>
            <button className='mobile-nav-toggle' onClick={ toggleTheNav
            } aria-controls='primary-navigation' aria-expanded='false'> <span className="sr-only">Menu</span></button>
       <nav>
           <ul data-visible="false" id="primary-navigation" className="text-white gap-6 primary-navigation flex">
               <li className="active">
                   <a href="/"><span aria-hidden='true'>01</span>Home</a>
                   </li>
               <li>
                   <a href="/about"><span aria-hidden='true'>02</span>about</a>
                   </li>
               <li
               ><a href="/contact"><span aria-hidden='true'>03</span>contact</a>
               </li>
               <li>
                   <a href="/about us"><span aria-hidden='true'>04</span>About</a>
                   </li>
           </ul>
       </nav>
        </header>
    )
}
