import React from 'react'
import Nav from './nav/nav'

export default function Layout({children}) {
    return (
       <div>
           <Nav/>
           <div className="content">
           {children}
           </div>
           <footer>
               <p className='text-center text-neutral-300 my-5'>made with love by shubham </p>
           </footer>
       </div>
    )
}
